body {
    font-family: $sans;
    font-size: 14px;
    font-feature-settings: "tnum";
    font-variant: tabular-nums;
    line-height: 1.4;
}

.wrapper {
    overflow: hidden;
    min-height: 100vh;
    min-height: 100dvh;
    display: flex;
    flex-direction: column;
    background-color: $bg-2;
    color: $white;
}

.container {
    padding: 0 15px;
    width: 100%;
    max-width: 1240px;
    margin: 0 auto;
    box-sizing: border-box;
    @include tablet {
        padding: 0 20px;
    }
    &--flex {
        @include flex-center;
    }
}

.main {
    flex: 1 0 auto;
}

.swiper-button-prev, .swiper-button-next {
    width: 43px;
    height: 43px;
    background-color: rgba(8, 10, 17, 0.698);
    border-radius: 50%;
    @include svg-bg(slider-arrow);
    background-size: 60px;
    z-index: $ui-index-2;
    &::after {
        display: none;
    }
}
.swiper-button-prev {
    transform: rotate(180deg);
}
