.link__btn {
    display: block;
    border-radius: 6px;
    padding: 6px 13px;
    background-color: $green;
    white-space: nowrap;
    font-size: 14px;
    line-height: 1.4;
    font-weight: 400;
    text-align: center;
    transition: background-color .3s ease-in-out;
    @include hover {        
        background-color: $green-hover;
    }
    &--wide {
        font-size: 16px;
        width: 100%;
        padding: 13px 15px;
        margin-top: 30px;
    }
    &--center {
        display: inline-block;
        margin-top: 24px;
        font-size: 16px;
        width: auto;
        padding: 12px 30px;
    }
    &--bold {
        font-weight: 700;
    }
    &--transparent {
        border: 1px solid $gray;
        background-color: transparent;
        color: $white;
        &:not(:disabled) {
            @include hover {
                opacity: 0.8;
                border: 1px solid $gray;
                background-color: transparent;
            }
        }
    }
}